.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app {
  background-color: #141414;
  color: white;
  min-height: 100vh;
  padding: 20px;
}

.category {
  margin-bottom: 40px;
}

.video-scroll {
  display: flex;
  overflow-x: auto;
  padding-bottom: 20px;
}

.video-card {
  flex: 0 0 auto;
  width: 200px;
  margin-right: 10px;
  background-color: #2f2f2f;
  border-radius: 4px;
  overflow: hidden;
  transition: transform 0.3s;
}

.video-card:hover {
  transform: scale(1.05);
}

.video-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.video-card h3 {
  margin: 10px;
  font-size: 14px;
}

.video-card p {
  margin: 10px;
  font-size: 12px;
}

button {
  background-color: #e50914;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  cursor: pointer;
  border-radius: 4px;
}

form {
  background-color: #2f2f2f;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 4px;
}

input {
  display: block;
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #4f4f4f;
  border: none;
  color: white;
  border-radius: 4px;
}