body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  background-color: #141414;
  color: white;
  min-height: 100vh;
  padding: 20px;
}

.category {
  margin-bottom: 40px;
}

.video-scroll {
  display: flex;
  overflow-x: auto;
  padding-bottom: 20px;
}

.video-card {
  flex: 0 0 auto;
  width: 200px;
  margin-right: 10px;
  background-color: #2f2f2f;
  border-radius: 4px;
  overflow: hidden;
  transition: transform 0.3s;
}

.video-card:hover {
  transform: scale(1.05);
}

.video-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.video-card h3 {
  margin: 10px;
  font-size: 14px;
}

.video-card p {
  margin: 10px;
  font-size: 12px;
}

button {
  background-color: #e50914;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  cursor: pointer;
  border-radius: 4px;
}

form {
  background-color: #2f2f2f;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 4px;
}

input {
  display: block;
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #4f4f4f;
  border: none;
  color: white;
  border-radius: 4px;
}